import { type } from "@testing-library/user-event/dist/type"

export const symbol = type => value => {

    let error = undefined
    if (value) {
        if (type === 'password') {
            value.length < 8
                ? error = ' '
                : error = undefined
        } else if (type === 'email') {

            if (value && value.indexOf('@') !== -1) {

                error = undefined
            } else {

                error = 'E-mail   !'
            }
        }

    }
    return error
}

export const emailValidate = symbol('email')
export const passwordValidate = symbol('password')

export const requiredFields = (value) => {

    let error = undefined

    if (!value) {

        error = ' '
    }



    return error
}

export const symbols30 = (value) => {
    let error = undefined
    if (value.length > 30) {
        error = ' :   '
    }
    return error
}
export const symbols300 = (value) => {
    let error = undefined
    if (value.length > 300) {
        error = ':   '
    }
    return error
}
export const rolesValidate = (value) => {

    let error = undefined
    if (!value) {
        error = '  '
    }
    return error
}
