
export const followUnfollow = (offers, offerId, bool) => {
    let result = []
    if (offers.length > 0) {
        result = offers.map(offer => { //  offers
            if (offer.id === offerId) {      //    offer  id      AC
                offer.isFollowing = bool // true   isFollowing
                if (bool === 1) {
                    offer.followers += 1
                } else {
                    offer.followers -= 1
                }

                return offer
            } else {
                return offer
            }
        })
    }

    return result;
};